body {
    margin: 0;
    font-family: 'Joti One', sans-serif;
  }
  
  .game-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: url('C:\Users\PCSM\OneDrive\Desktop\memory-game\client\src\img\background.png') no-repeat center center;
    background-size: cover;
    color: #fff;
  }
  
  .btn-back {
    text-decoration: none;
    background: #ff9800;
    color: #fff;
    padding: 10px 20px;
    border-radius: 4px;
    margin-bottom: 20px;
    transition: background 0.3s ease;
  }
  
  .btn-back:hover {
    background: #e68900;
  }
  
  .moves-counter {
    font-size: 1.5rem;
    margin-bottom: 20px;
  }
  
  .game-over,
  .game-success {
    text-align: center;
    background: rgba(0, 0, 0, 0.6);
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  }
  
  .game-over-img {
    width: 200px;
    margin: 20px 0;
  }
  
  .game-over-guess {
    margin-top: 20px;
  }
  
  .game-over-guess input {
    padding: 10px;
    margin: 10px;
    border-radius: 4px;
    border: none;
  }
  
  .btn-submit,
  .btn-end-game {
    background: #4caf50;
    color: #fff;
    padding: 10px 20px;
    border-radius: 4px;
    transition: background 0.3s ease;
  }
  
  .btn-submit:hover,
  .btn-end-game:hover {
    background: #388e3c;
  }
  